<template>
<div class="products">
    <Navbar></Navbar>
    <div class="container">
        <h1 class="text-center p-5">Our Products</h1>
        <section>
            <ul>
                <li @click="switchTab(1)">Tab 1</li>
                <li @click="switchTab(2)">Tab 2</li>
            </ul>

            <HelloWorld class="tab tab-1" v-show="tab === 1" />
            <Test class="tab tab-2" v-show="tab === 2" />
        </section>
    </div>

</div>
</template>

<script>
import HelloWorld from '@/components/HelloWorld.vue'
import Test from '@/components/Test.vue'
export default {
    components: {
        HelloWorld,
        Test
    },
    data() {
        return {
            tab: 1
        }
    },
    methods: {
        switchTab(tab) {
            this.tab = tab
        }
    }
}
</script>
